export const GET_TOPICS = "GET_TOPICS"; //
export const LOAD_MORE = "LOAD_MORE"; //
export const GET_TOPIC_DETAIL = "GET_TOPIC_DETAIL"; //
export const CLEAR_TOPIC = "CLEAR_TOPIC"; //
export const COLLECT_TOPIC = "COLLECT_TOPIC"; //
export const CANCEL_COLLECT = "CANCEL_COLLECT"; //
export const REPLY_TOPIC = "REPLY_TOPIC"; //
export const UP_VOTE = "UP_VOTE"; //

export const getTopics = searchTab => ({
  type: GET_TOPICS,
  searchTab,
})
export const loadMore = searchTab => ({
  type: LOAD_MORE,
  searchTab,
})
export const getTopicDetail = (topic_id) => ({
  type: GET_TOPIC_DETAIL,
  topic_id,
})
export const clearTopic = () => ({
  type: CLEAR_TOPIC,
})
// ,(topic_id)
export const collectTopic = topic_id => ({
    type: COLLECT_TOPIC,
    topic_id
})
export const cancelCollect = topic_id => ({
    type: CANCEL_COLLECT,
    topic_id
})
// (reply_id,content)
export const replyTopic = (topic_id,info) => ({
    type: REPLY_TOPIC,
    topic_id,
    info
})
// 
export const upvote = (topic_id,reply_id) => ({
    type: UP_VOTE,
    topic_id,
    reply_id
})



